<template>
    <v-sheet height="100%" class="pa-4" elevation="1" id="mi">
        <v-sheet class="d-flex justify-space-between ">
            <img src="@/assets/pleca-gob2.png" height="50px">
            <img src="@/assets/pleca_tecnm.jpg" height="50px">
        </v-sheet>
        <v-sheet class="text-right pa-2">
            <div class="font-weight-bold text-body-2">Centro Nacional de Investigacin y Desarrollo Tecnolgico</div>
            <div class="text-body-2">Sistema para el control de entrada y salida de equipo</div>
        </v-sheet>
        <div class="text-h6" @click="generateReport">Reporte General</div>
        
        <apexchart  type="bar" :options="options" :series="series"></apexchart>

        
    </v-sheet>
</template>
<script>
import html2pdf from 'html2pdf.js';

export default {
    
    data: () => ({ 
        dialog: false,
        options: {
        chart: {
          id: 'frecuencia-uso'
        },
        xaxis: {
          categories: ['ene', 'feb', 'mar','abr','may','jun','jul','ago','sep','oct','nov','dic']
        }
      },
    series: [{
        name: 'Equipo usado',
        data: [30, 40, 45, 50, 49, 60, 70, 91]
      }]
     }),
    methods: {
        generateReport () {  
          var element = document.getElementById('mi');
          var opt = {
          margin:       0.3,
          filename:     'reportegeneral.pdf',
          image:        { type: 'jpeg', quality: 0.98 },
          html2canvas:  { scale: 5 },
          jsPDF:        { unit: 'in', format: 'letter', orientation: 'portrait' }
        };

        // New Promise-based usage:
        html2pdf().set(opt).from(element).save();

        


        }
    }
}
</script>